const path = require('path');

const {BrowserWindow} = require('electron');

const fileDialog = require('./fileDialog');
const language = require('./language');

var confWin;

var configuration = {
    controls: true,
    progress: true,
    slideNumber: false,
    history: false,
    keyboard: true,
    overview: true,
    center: true,
    touch: true,
    loop: false,
    rtl: false,
    shuffle: false,
    fragments: true,
    embedded: false,
    help: true,
    showNotes: false,
    autoSlide: 0,
    autoSlideStoppable: true,
    //autoSlideMethod: Reveal.navigateNext,
    mouseWheel: false,
    hideAddressBar: true,
    previewLinks: false,
    transition: 'none',
    transitionSpeed: 'default',
    backgroundTransition: 'default',
    viewDistance: 3,
    parallaxBackgroundImage: '',
    parallaxBackgroundSize: '',
    parallaxBackgroundHorizontal: null,
    parallaxBackgroundVertical: null
};

var uiConfiguration = {
    colorScheme: 'dark',
    showButtonText: true
}

function get() {
    return configuration;
}

function getUiConf() {
    return uiConfiguration;
}

function set(newConfiguration) {
    var event = (newConfiguration.hasOwnProperty('shuffle') && !newConfiguration.shuffle)? 'unShuffle' : 'configurationChange';

    configuration = Object.assign({}, configuration, newConfiguration);
    BrowserWindow.getAllWindows()[0].webContents.send(event, configuration);
}

function setUiConf(newConfiguration) {
    uiConfiguration = Object.assign({}, uiConfiguration, newConfiguration);
    BrowserWindow.getFocusedWindow().webContents.send('uiConfigurationChanged', uiConfiguration);
}

function openConfWin() {
    
    confWin = new BrowserWindow({
        width: 500,
        height: 720,
        //autoHideMenuBar: true,
        useContentSize: true,
        parent: BrowserWindow.getFocusedWindow(),
        center: true,
        alwaysOnTop: false,
        show: false,
        webPreferences: {
            devTools: true
        }
    });

    confWin.setMenuBarVisibility(false);
    //confWin.webContents.openDevTools()
    confWin.loadURL('file://' + path.join(__dirname, 'front', 'options', 'options.html'));

    confWin.once('ready-to-show', () => {
        confWin.webContents.send('init', getUiConf());
        confWin.show()
    });
}

module.exports = {
    get,
    set,
    getUiConf,
    setUiConf,
    openImg: () => {
        fileDialog.openImg(confWin);
    },
    openConfWin,
    getText: language.getText
}


